/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import metadata from "./block.json";

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps, InspectorControls } from "@wordpress/block-editor";
import { PanelBody, ToggleControl } from "@wordpress/components";

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import "./editor.scss";

import { Curve } from "./components/curve";
import { TopCurveSettings } from "./components/topCurveSettings";
import { BottomCurveSettings } from "./components/bottomCurveSettings";

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {Element} Element to render.
 */
export default function Edit(props) {
	const { attributes, setAttributes } = props;
	const { enableTopCurve, width, height, flipX, flipY, color, enableBottomCurve, bottomWidth, bottomHeight, bottomFlipX, bottomFlipY, bottomColor } = attributes;
	const { className, ...blockProps } = useBlockProps();
	return (
		<>
			<section className={`${className} alignfull`} {...blockProps}>
				{enableTopCurve && (
					<Curve
						isBottom={false}
						height={height}
						flipX={flipX}
						flipY={flipY}
						width={width}
						color={color.background}
					/>
				)}
				{enableBottomCurve && (
					<Curve
						isBottom={true}
						height={bottomHeight}
						flipX={bottomFlipX}
						flipY={bottomFlipY}
						width={bottomWidth}
						color={bottomColor.background}
					/>
				)}
			</section>

			<InspectorControls>
				<PanelBody title="Top Curve">
					<ToggleControl
						label={__("Enable Top Curve", metadata.textdomain)}
						min={0}
						max={100}
						checked={attributes.enableTopCurve}
						onChange={(value) => setAttributes({ enableTopCurve: value })}
						__nextHasNoMarginBottom={true}
					/>
					<TopCurveSettings attributes={attributes} setAttributes={setAttributes} />
				</PanelBody>
				<PanelBody title="Bottom Curve">
					<ToggleControl
						label={__("Enable Bottom Curve", metadata.textdomain)}
						min={0}
						max={100}
						checked={attributes.enableBottomCurve}
						onChange={(value) => setAttributes({ enableBottomCurve: value })}
						__nextHasNoMarginBottom={true}
					/>
					<BottomCurveSettings attributes={attributes} setAttributes={setAttributes} />
				</PanelBody>
			</InspectorControls>
		</>
	);
}
